/*
 * 
 */
import store from '@/store'
const top = 500 //notice 

/*
 * notice 
 * type:  01234
 */
export const notice = (title = '', desc = '', type = 0, duration =  3, onClose, key) => {
	let config = null;
	
	if (key) {
		config = {
			title: title,
			desc: desc,
			duration: duration,
			key: key,
			top: top,
			onClose: onClose
		}
	} else {
		config = {
			title : title,
			desc: desc,
			top: top,
			duration: duration,
			onClose: onClose
		}
	}
	store.state.notice.type = type;
	store.state.notice.config = config;
	store.state.notice.state++;
}
/*
 *  notice 
 */
export const noticeClose = (key) => {
	store.state.noticeClose.key = key;
	store.state.noticeClose.state++;
}

/*
 * message 
 * type:  01234
 */
export const message = (content = 'twinkle twinkle little star', type = 0, onClose, duration = 1.5 ) => {
	if (type === 1) {
		duration = 0;
	}
	let config = {
		content: content,
		duration: duration,
		onClose: onClose
	}
	store.state.message.type = type;
	store.state.message.config = config;
	store.state.message.state++;
}


/*
 * modal 
 */
/*
 * 
 * type: 01234
 */
export const simpleModal = (title = '', content = '', type = 0, onOk) => {
	let config = {
		title: title,
		content: content,
		onOk: onOk
	}
	store.state.simpleModal.type = type;
	store.state.simpleModal.config = config;
	store.state.simpleModal.state++;
}
/*
 *  
 * loading
 */
export const confirmMoadl = (title = '', content = '', onOk, onCancel, okText = '', cancelText = '', loading = false) => {
	let config = {
		title: title,
		content: content,
		onOk: onOk,
		onCancel: onCancel,
		okText: okText,
		cancelText: cancelText,
		loading: loading
	}
	store.state.confirmMoadl.config = config;
	store.state.confirmMoadl.state++;
}



